body .NotFound{position: absolute;left:0;top:0;right:0;bottom:0;background-color: #311b1f;z-index: 1;}
.NotFound video{position:fixed;left:50%;top:50%;transform:translate(-50%,-50%) translateZ(0);will-change: transform;opacity:1;transition:opacity 0.75s 0.75s;min-height:100%;min-width:100%;}
.NotFound .NotFoundDiv{width: 100%;padding:0px 0 75px;color:#fff;position: absolute;top:50%;left:50%; transform: translate(-50%, -50%) translateZ(0);will-change: transform; z-index: 2;}
.NotFound .NotFoundDiv .NotFoundTitle{font-weight: 100;font-size: 25rem;font-family: 'Work Sans',sans-serif;line-height: 100%;text-shadow: 0px 0px 5px #000;}
.NotFound .NotFoundDiv .NotFoundTitle span{display:inline-block; animation: wave 8s ease-in-out infinite;transform: translateY(30px) translateZ(0);will-change: transform;}
.NotFound .NotFoundDiv .NotFoundText{letter-spacing: 3px;font-weight: 600; font-size: 2.75rem;max-width: 750px;text-transform: uppercase;margin:0px auto 0;text-shadow: 0px 0px 5px #000;}
.NotFound .NotFoundDiv .NotFoundDesc{letter-spacing: 2px; font-weight: 100; font-size: 1.75rem;margin:20px 0;text-transform: lowercase;text-shadow: 0px 0px 5px #000;}
.NotFound .NotFoundDiv .backHome{margin: 30px 0;}
.NotFound .overlay{position: fixed;}
.NotFound.fade-exit{opacity:0;transition:opacity 0.75s 0.75s;transform: translateZ(0);will-change:transform;}

@keyframes wave {
    0%{}
    50%{transform: translateY(0px) translateZ(0);will-change: transform;}
    100%{}
}


@media screen and (max-width: 960px) {
    .NotFound .NotFoundDiv .NotFoundTitle{font-size:18.5rem;}
    .NotFound .NotFoundDiv .NotFoundText{font-size:2rem;max-width:625px;}
    .NotFound .NotFoundDiv .NotFoundDesc{font-size: 1.25rem;}
}

@media screen and (max-width: 769px) {
    .NotFound .NotFoundDiv .NotFoundTitle{font-size:15rem;}
    .NotFound .NotFoundDiv .NotFoundText{font-size:1.5rem;max-width:385px;}
    .NotFound .NotFoundDiv .NotFoundDesc{font-size: 1rem;}
}

@media screen and (max-width: 480px) {
    .NotFound .NotFoundDiv .NotFoundTitle{font-size:12rem;}
    .NotFound .NotFoundDiv .NotFoundText{font-size:1.125rem;max-width:300px;}
    .NotFound .NotFoundDiv .NotFoundDesc{font-size: 0.75rem;}
}


@media screen and (max-height: 480px) {
    .NotFound .NotFoundDiv{padding:0 0 25px;}
    .NotFound .NotFoundDiv .NotFoundTitle{font-size:10rem;margin-bottom:15px;}
    .NotFound .NotFoundDiv .NotFoundText{font-size:1rem;max-width:275px;}
    .NotFound .NotFoundDiv .NotFoundDesc{font-size: 0.75rem;margin: 10px 0;}
    .NotFound .NotFoundDiv .backHome{margin:15px 0;}
    .NotFound .NotFoundDiv .backHome .btn{padding: 10px 8px;font-size:0.75rem;}
    .NotFound .NotFoundDiv .NotFoundTitle span{transform: translateY(15px) translateZ(0) !important;will-change: transform;line-height:70%;}
}

